.form{
    display: flex;
    /* margin: auto; */
    align-items: center;
    justify-content: center;
    margin-top: 1em;
    flex-direction: row;
    gap: 1em;
    flex-wrap: wrap;
    
}

.form input {
    width: 10em;
    border-radius: 10px;
    font-size: 1em;
    padding: 0.3em 2em;
}

.form button{
    color: black;
    font-size: 1.2em;
    background-color: rgb(145, 251, 25);
    border-radius: 7px;
    padding: 0.2em 1.2em;
}
