.container{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
    font-family: sans-serif;
    width: 18em;
    margin-top: 1em;
    flex-direction: column;
    padding: 1em 2em;
    border: 1px solid black;
    background-color: beige;
    border-radius: 8px;

    
    /* background-color: aqua; */
}
.container .appHeading{
    font-size: 2em;
    font-weight: 600;
}

.taskDiv{
    margin: auto;
    margin-top: 2em;
    background-color: rgb(192, 178, 178);
    width: 100%;
    border: 2px solid gray;
    border-radius: 10px;
   
}

.welcomeMsg{
    text-align: center;
    color: rgb(0, 0, 0);
    padding:1em 0 ;
    font-size: 24px;
    border-radius: 10px;
    background-color: rgb(255, 238, 2);
}