.taskContainer{
    margin-top: 0.2em;
    display: flex;
    /* justify-content: space-around; */
    align-items: center;
    /* width: 100%; */
    padding:1em;
    
    background-color: rgba(107, 92, 247, 0.31);
    border-radius: 10px 10px 0px 0px;
    /* border: 2px solid rgb(11, 11, 10); */
